import React, { Component } from 'react';
import './App.css';

class App extends Component {
  state = {
    good: 0,
    neutral: 0,
    bad: 0,
  };

  handleOn = () => {
    this.setState(prevState => ({
      good: prevState.good + 1,
      neutral: prevState.neutral + 1,
      bad: prevState.bad + 1,
    }));
  };

  render() {
    const { good, neutral, bad } = this.state;
    return (
      <div className="App">
        <h1>Please leave feedback</h1>
        <div>
          <button type="button" onClick={this.handleOn}>
            Good
          </button>
          <button type="button" onClick={this.handleOn}>
            Neutral
          </button>
          <button type="button" onClick={this.handleOn}>
            Bad
          </button>
        </div>
        <h2>Statistic</h2>
        <div>
          <ul>
            <li>
              <span>Good:</span>
              <span>{good}</span>
            </li>
            <li>
              <span>Neutral:</span>
              <span>{neutral}</span>
            </li>
            <li>
              <span>Bad:</span>
              <span>{bad}</span>
            </li>
            {/* <li>
              <span>Total:</span>
              <span>7</span>
            </li>
            <li>
              <span>Positive feedback:</span>
              <span>43%</span>
            </li> */}
          </ul>
        </div>
      </div>
    );
  }
}

export default App;
